/* frontend/src/App.css */
body {
  font-family: Arial, sans-serif;
  margin: 0;
  padding: 0;
  background: black;
  color: #468585;
}

header {
  background: #50B498;
  color: black;
  padding: 1rem 0;
  text-align: center;
}

nav ul {
  list-style: none;
  padding: 0;
}

nav ul li {
  display: inline;
  margin: 0 1rem;
}

nav ul li a {
  color: black;
  text-decoration: none;
}

.container {
  width: 80%;
  margin: 0 auto;
  padding: 2rem 0;
}

.container div {
  background: black;
  padding: 2rem;
  border-radius: 8px;
  box-shadow: 0 0 30px #50B498;
  margin-bottom: 2rem;
  color: #fff;
}

footer {
  background: #50B498;
  color: black;
  text-align: center;
  padding: 1rem 0;
  position: fixed;
  width: 100%;
  bottom: 0;
}

a {
  color: #007bff;
  text-decoration: none;
}

a:hover {
  text-decoration: underline;
}

/* Animations */
.fade-in {
  animation: fadeIn 2s;
}

@keyframes fadeIn {
  0% { opacity: 0; }
  100% { opacity: 1; }
}